@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
@font-face {
  font-family: CoreSansG-Regular;
  src: url("./fonts/CoreSansG-Regular.ttf");
}

@font-face {
  font-family: CoreSansG-Light;
  src: url("./fonts/CoreSansG-ExtraLight.ttf");
}

@font-face {
  font-family: CoreSansG-Bold;
  src: url("./fonts/CoreSansG-Bold.ttf");
}

html,
body,
ul,
li,
h1 {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

a {
  text-decoration: none;
  color: #fff ;
}

.App {
  text-align: center;
}

.heading {
  margin: 80px auto;
  font-family: CoreSansG-Light, sans-serif;
}
