@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@font-face{font-family:CoreSansG-Regular;src:url(../../static/media/CoreSansG-Regular.bc56ce06.ttf)}@font-face{font-family:CoreSansG-Light;src:url(../../static/media/CoreSansG-ExtraLight.11dc0330.ttf)}@font-face{font-family:CoreSansG-Bold;src:url(../../static/media/CoreSansG-Bold.dd2db6d8.ttf)}html,body,ul,li,h1{margin:0;padding:0;list-style-type:none}a{text-decoration:none;color:#fff}.App{text-align:center}.heading{margin:80px auto;font-family:CoreSansG-Light, sans-serif}

.header{width:100%;height:100px;background:linear-gradient(90deg, #5a4be6 .26%, #73aff7 47.1%, #93d0d9 99.8%);text-align:center;line-height:100px;text-transform:uppercase;font-family:CoreSansG-Regular, sans-serif;font-size:32px;letter-spacing:2px;color:#cfe0f4;position:relative}.header__logo{height:26px}@media screen and (max-width: 600px){.header{display:flex;box-sizing:border-box;justify-content:space-between;align-items:center;padding:0 25px}.header__logo{height:16px}}.header .heading{padding:15px}.language-toggler{height:19px;position:absolute;top:0;right:5%}.language-toggler__link{font-family:Roboto, sans-serif;font-size:16px;letter-spacing:-0.2px;background-color:transparent;border:none;outline:none;color:#fff}.language-toggler__link--active{color:#587bc3}.language-toggler__link:first-child::after{content:" | ";color:#fff}@media screen and (max-width: 600px){.language-toggler{right:15%}}

.card__section{padding:0 calc(50% - 480px)}.card__heading{font-family:CoreSansG-Bold, sans-serif;width:404px;display:block;text-align:left;font-size:18px;margin-bottom:16px}.card__form{margin-top:28px;border:1px solid #e4e4e4;border-radius:4px;display:flex;flex-direction:column;padding:40px 100px;align-items:center}.card__input{font-size:16px;font-family:CoreSansG-Regular, sans-serif;box-sizing:border-box;padding-left:16px;display:block;border:1px solid #ccc;border-radius:4px;width:404px;height:56px;outline:none;transition:border, box-shadow 0.3s ease;background-repeat:no-repeat;background-position:90% 50%}.card__input:focus{box-shadow:3px 7px 23px 0 rgba(46,74,117,0.2);border:1px solid #93d0d9}.card__input--small{width:194px;background-image:none}.card__input--error{border-color:#ff8d8d}.card__wrapper{width:404px;display:flex;justify-content:space-between}.card__info{display:block;margin-top:19px;width:394px;text-align:left;font-family:CoreSansG-Regular, sans-serif;font-size:16px;color:#808080;line-height:20px}.card__button{margin-top:32px;box-sizing:border-box;width:404px;height:56px;background:linear-gradient(122.5deg, #5a4be6 -33.07%, #73aff7 48.35%, #93d0d9 139.94%);border-radius:4px;outline:none;font-family:CoreSansG-Regular, sans-serif;font-size:16px;line-height:17px;color:#fff;border:none;transition:box-shadow 0.5s ease}.card__button:hover{cursor:pointer;box-shadow:3px 7px 23px 0 #73aff7}.card__button:active{box-shadow:3px 7px 23px 0 #374e68}.card__error{width:404px;height:16px;font-family:Roboto, sans-serif;font-size:12px;line-height:16px;color:#ff8d8d;text-align:left}.card__errors-wrapper{display:flex;justify-content:space-between}.card__error--small{width:200px}.card__error--small:first-child{margin-right:15px}@media screen and (max-width: 600px){.card__button{width:295px}.card__input{width:295px}.card__wrapper{width:295px}.card__input--small{width:144px}.card__error{width:295px}.card__errors-wrapper{width:295px}.card__info{width:287px}.card__heading{width:295px}.card__section{padding:0 10px}}

